@import url(https://fonts.googleapis.com/css2? family = Roboto: ital, wght @ 0,100; 0,300; 0,400; 0,500; 0,700; 0,900; 1,100; 1,300; 1,400; 1,500; 1,700; 1,900 & display = swap );
@import url(https://fonts.googleapis.com/css2? family = Roboto: ital, wght @ 0,100; 0,300; 0,400; 0,500; 0,700; 0,900; 1,100; 1,300; 1,400; 1,500; 1,700; 1,900 & display = swap );
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #171717; */
  overflow-y: hidden;
  overflow-x: hidden;
}

html {
  overflow-y: hidden;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.column {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.BackgroundPage{background:linear-gradient(235.48deg, #01baef 0%, #0d5f76 28.44%, #171717 52.6%, #611211 77.4%, #b80c09 100%);width:100vw;height:100vh}

.Navigation{color:#fff;height:100px;justify-content:center;grid-gap:10vw;gap:10vw;align-items:center}.Navigation .item{color:#fff;text-decoration:none;font-family:"Roboto",sans-serif;font-weight:bold;font-size:24px}.Navigation .item:hover{color:#01baef}.Navigation .item.active{color:#01baef}

.Home{height:calc(100% - 100px)}.Home .Home-content{padding:0 10vw;height:100%}.Home .Home-content .header{justify-content:flex-start;align-items:center;height:120px}.Home .Home-content .header .title{font-family:"Roboto",sans-serif;font-style:normal;font-weight:900;font-size:36px;line-height:42px;align-items:center;text-align:center;margin:0;color:#ffffff}.Home .Home-content .header .title .title-span{color:#01baef}.Home .Home-content .body{height:calc(100% - 120px);justify-content:space-around}.Home .Home-content .body .description-content{justify-content:center;align-items:center}.Home .Home-content .body .description-content .description{font-family:"Roboto",sans-serif;font-style:normal;font-weight:900;font-size:36px;line-height:42px;display:flex;align-items:center;text-align:center;color:#929292}.Home .Home-content .body .button-actions{justify-content:center;align-items:center;grid-gap:7vw;gap:7vw}.Home .Home-content .body .button-actions .button{padding:1rem 2rem;transition:all 500ms ease-in-out;font-family:"Roboto",sans-serif;font-style:normal;font-weight:900;font-size:24px;line-height:28px;display:flex;align-items:center;text-align:center;border-radius:10px;color:#ffffff;border:none;text-decoration:none}.Home .Home-content .body .button-actions .button:hover{padding:1.2rem 2.2rem}.Home .Home-content .body .button-actions .button-blue{background-color:#01baef}.Home .Home-content .body .button-actions .button-red{background-color:#b80c09}.Home .Home-content .body .icons-contet{justify-content:center;align-items:center;grid-gap:2rem;gap:2rem}.Home .Home-content .body .icons-contet .ico svg{width:30px;height:30px;transition:all 500ms ease-in-out}.Home .Home-content .body .icons-contet .ico:hover svg{width:35px;height:35px}

.CardProject{justify-content:space-between}

.Projects{display:flex;justify-content:center}.Projects .Projects-container{overflow-y:auto;max-width:80vw}

